}

.download-pdf {
    color: white;
    text-align: center;
    
}

@media (min-width: 1000px) {

    .download-pdf {
        position: fixed;
        z-index: 9999;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        top: 2rem;
        right: -7rem;
    
    }
    
}

.download-pdf__link {
    border-top: 2px solid white;
    border-bottom: 2px solid white;
    padding: 16px 7em;
    padding: 1rem 7em;
    display: block;
    width: 100%;
    color: inherit;
    text-decoration: none;
    background: #c47333;
    
}

.download-pdf__link:hover {
    background: #4db0c4;
    
}

@media (min-width: 1000px) {

    .download-pdf__text {
        display: inline-block;
        vertical-align: middle;
        width: 6em;
    
    }
    
}

.download-pdf__icon { /* from icono font */
    border: 2px solid;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    font-style: normal;
    color: inherit;;
    text-align: left;
    text-indent: -9999px;
    direction: ltr;
    width: 26px;
    height: 32px;
    border-radius: 0 0 0 10px;
    margin: 1px 4px 1px 0.5em;
    
}

.download-pdf__icon::before,
    .download-pdf__icon::after {
    content: '';
    pointer-events: none;
    
}

.download-pdf__icon::before {
    position: absolute;
    width: 0;
    height: 0;
    left: -3px;
    bottom: -3px;
    border-width: 5px;
    border-style: solid;
    border-bottom-color: transparent;
    border-left-color: transparent;
    
}

.download-pdf__icon::after {
    width: 13px;
    height: 2px;
        -webkit-box-shadow: inset 0 0 0 32px,0 -5px 0 0,0 5px 0 0;
                box-shadow: inset 0 0 0 32px,0 -5px 0 0,0 5px 0 0;
    position: absolute;
    left: 50%;
    top: 50%;
        -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    
}

