@media (min-width: 1000px) {

    .cube--loading {
          -webkit-transform-style: preserve-3d;
                  transform-style: preserve-3d;
          -webkit-animation: spin-in 1s backwards 1 1s;
                  animation: spin-in 1s backwards 1 1s;
          will-change: transform;
    
    }

    .cube__perspective {
        -webkit-perspective: 1000px;
                perspective: 1000px;
        -webkit-transform-style: preserve-3d;
                transform-style: preserve-3d;
        -webkit-transform-origin: top;
                transform-origin: top;
    
    }
    
}

@media (max-width: 1000px) {

    .cube__face-1 {
        display: none;
    
    }
    
}

.cube__face-1 {
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
    background: #c47333;
        -webkit-transform:  rotateY(-90deg) translateX(-100%);
                transform:  rotateY(-90deg) translateX(-100%);
        -webkit-transform-origin: left;
                transform-origin: left;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    
}

.cube--loading .cube__face-1::before {
    content: "";
    height: 15vw;
    width: 15vw;
    margin-top: 33vh;
    border-radius: 50%;
    border: 1vw dotted white;
    border-bottom: 0 none;
            -webkit-animation: spinner 3s backwards infinite linear;
                    animation: spinner 3s backwards infinite linear;
    
}

.cube__face-2 {
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
    
}

.cube-loaded .cube__face-2 {
        display: none;
